<template>
  <div class="app-container">

    <el-form :inline="true" :model="form" class="form-inline-query">
      <el-form-item label="课程编号" >
        <el-input v-model="form.num" placeholder="请输入课程编号" />
      </el-form-item>
      <el-form-item label="课程名">
        <el-input v-model="form.name" placeholder="请输入课程名"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="commButton" type="primary" size="mini" @click="doQuery()">查询</el-button>
      </el-form-item>
      <el-form-item>
        <el-button class="commButton" type="primary" size="mini" @click="doAdd()">添加</el-button>
      </el-form-item>
    </el-form>    

<el-scrollbar height="800px">
    <el-table  class="table-content"
        :data="dataList"
        border
        style="width: 100%;"
    >
      <el-table-column
      label="序号"
      fixed="left"
      width="50"          
      align="center"
      color="black"
      >
      <template v-slot="scope">
          {{ scope.$index+1 }}
      </template>
      </el-table-column>

      <el-table-column
      label="课程编号"
      align="center"
      color="black"
      sortable
      prop="courseNum" />

      <el-table-column
      label="课程名称"
      align="center"
      color="black"
      prop="courseName" />

      <el-table-column
      label="课程教师"
      align="center"
      color="black"
      sortable
      prop="courseteacher" />

      <el-table-column
      label="课程描述"
      align="center"
      color="black"
      sortable
      prop="courseinfo" />

      <el-table-column
      label="课程上课日"
      align="center"
      color="black"
      sortable
      prop="courseday" />

      <el-table-column
      label="课程上课节次"
      align="center"
      color="black"
      sortable
      prop="courseorder" />

      <el-table-column
      label="课程学分"
      align="center"
      color="black"
      sortable
      prop="coursescore" />

      <el-table-column
      label="课程选课结束时间"
      align="center"
      color="black"
      sortable
      prop="courseselectend" />

      <el-table-column
          label="操作"
          align="center"
          color="black"
      >
      <template v-slot="scope">
          <el-button type="text" size="mini" @click="doEdit(scope.row.courseId)"><el-icon><Edit /></el-icon>编辑</el-button>
          <el-popconfirm title="确定要删除该课程吗？" @confirm="doDelete(scope.row.courseId)" @cancel="cancelEvent">
          <template #reference>
          <el-button type="text" size="mini"><el-icon><Delete /></el-icon>删除</el-button>
          </template>
          </el-popconfirm>
      </template>
      </el-table-column>
    </el-table>
    </el-scrollbar>
  </div>

  <el-dialog title="课程管理" v-model="editVisible" width="30%">
    <el-form ref="form1" :model="form1" label-width="125px" class="form-div">
      <el-form-item label="课程号">
        <el-input v-model="form1.courseNum" placeholder="请输入课程号" ></el-input>
      </el-form-item>
      <el-form-item label="课程名">
        <el-input  v-model="form1.courseName" placeholder="请输入课程名"  ></el-input>
      </el-form-item>
      <el-form-item label="课程教师">
        <el-input  v-model="form1.courseteacher" placeholder="请输入课程教师"  ></el-input>
      </el-form-item>
      <el-form-item label="课程描述">
        <el-input  v-model="form1.courseinfo" placeholder="请输入课程描述"  ></el-input>
      </el-form-item>
      <el-form-item label="课程学分">
        <el-input  v-model="form1.coursescore" placeholder="请输入课程学分"  ></el-input>
      </el-form-item>
      <el-form-item label="课程上课日">
        <el-select v-model="form1.courseday" placeholder="请选择课程上课日">
          <el-option
            v-for="item in options"
            :key="item.courseday"
            :label="item.label"
            :value="item.courseday"
          >
          </el-option>
        </el-select>
        <!-- <el-input  v-model="form.courseday" placeholder="请输入课程上课日"  /> -->
      </el-form-item>
      <el-form-item label="课程上课节次">
        <el-select v-model="form1.courseorder" placeholder="请选择课程上课节次">
          <el-option
            v-for="item in options1"
            :key="item.courseorder"
            :label="item.label"
            :value="item.courseorder"
          >
          </el-option>
        </el-select>
        <!-- <el-input  v-model="form.courseorder" placeholder="请输入课程上课节次"  /> -->
      </el-form-item>
      <el-form-item label="课程选课结束时间">
        <el-date-picker v-model="form1.courseselectend" type="datetime" placeholder="课程选课结束时间"></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button @click="editVisible = false;">取 消</el-button>
        <el-button class="editButton" size="mini" @click="submit">提交</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import {getCourseList,courseDelete} from '@/service/genServ.js'
import { ElMessage } from 'element-plus'
import { ref} from 'vue'
import {
  getCourseInfo,
  courseSubmit,
} from "@/service/genServ.js";
import {
  Delete,Edit
} from '@element-plus/icons'
export default {
  name: 'CourseTable',
  components:{
    Delete,Edit
  },
  data() {
    return {
      options: ref([
        {
          courseday: '星期一',
          label: '星期一',
        },
        {
          courseday: '星期二',
          label: '星期二',
        },
        {
          courseday: '星期三',
          label: '星期三',
        },
        {
          courseday: '星期四',
          label: '星期四',
        },
        {
          courseday: '星期五',
          label: '星期五',
        },
        {
          courseday: '星期六',
          label: '星期六',
        },
        {
          courseday: '星期天',
          label: '星期天',
        },
      ]),
      options1: ref([
        {
          courseorder: '第一节',
          label: '第一节',
        },
        {
          courseorder: '第二节',
          label: '第二节',
        },
        {
          courseorder: '第三节',
          label: '第三节',
        },
        {
          courseorder: '第四节',
          label: '第四节',
        },
        {
          courseorder: '第五节',
          label: '第五节',
        },
      ]),
      form1: {
        courseId: null,
        courseNum: "",
        courseName: "",
        courseteacher:"",
        courseinfo:"",
        courseday:ref(''),
        courseorder:ref(''),
        coursescore:"",
        courseselectend:""
      },
      form2: {
        courseId: null,
        courseNum: "",
        courseName: "",
        courseteacher:"",
        courseinfo:"",
        courseday:ref(''),
        courseorder:ref(''),
        coursescore:"",
        courseselectend:""
      },
      form: {
        num:'',
        name:'',
      },
      editVisible : false,
      dataList:[]
    }
  },
  created() {
    this.doQuery();
  },
  methods: {
    doQuery() {
       getCourseList({'num':this.form.num, 'name':this.form.name}).then(res => {
        this.dataList = res.data;
        console.log(this.dataList);
      })
    },

    doEdit(courseId) {
      this.editVisible=true;
      getCourseInfo({ courseId:courseId }).then(
        (res) => {
          this.form1 = res.data;
        }
      );
    },

    doAdd() {
      this.editVisible=true;
      this.form1.courseId= null;
      this.form1.courseNum= "";
      this.form1.courseName="";
      this.form1.courseteacher="";
      this.form1.courseinfo="";
      this.form1.courseday=ref('');
      this.form1.courseorder=ref('');
      this.form1.coursescore="";
      this.form1.courseselectend="";
    },

    submit() {
      courseSubmit({
        form: this.form1,
      }).then((res) => {
         ElMessage({
          message: '提交成功！',
          type: 'success',
        })
        this.form1.courseId = res.data;
        this.editVisible=false;
        this.doQuery();
      });
      
    },  

    doDelete(courseId) {
      courseDelete({'courseId':courseId}).then(res => {
        console.log(res)
        this.doQuery();
        ElMessage({
          message: '删除成功',
          type: 'success',
        })
    }),
    this.doQuery();
    },

  }
}
</script>

<style scoped>
.app-container{
  width: 1650px;
  position:absolute;
  top:100px;
  left:225px;
}
</style>
